import { useCallback, useEffect, useState } from "react";
import "./header.scss";
import classNames from "classnames";
export function Header() {
  const [isTop, setIsTop] = useState(false);
  const [isToggle, setIsToggle] = useState(false);
  const [extraCase, setExtraCase] = useState(false);
  useEffect(() => {
    window.addEventListener("scroll", handleScroll);

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  const handleScroll = () => {
    if (window.scrollY === 0) {
      setIsTop(false);
    } else {
      setIsTop(true);
    }
  };
  return (
    <header
      className={classNames("header default", {
        scrolled: isTop || extraCase,
      })}
    >
      <nav className="wrap">
        <div className="header-item-wrapper">
          <span className="header-logo-img">자이플</span>
          <span className="header-button-mobile">
            <input class="burger-check" type="checkbox" id="burger-check" />
            <label
              class="burger-icon"
              for="burger-check"
              onClick={() => {
                if (!isTop) {
                  setExtraCase(true);
                  setIsToggle(!isToggle);
                } else {
                  setExtraCase(false);
                  setIsToggle(!isToggle);
                }
              }}
            >
              <span class="burger-sticks"></span>
            </label>
          </span>
        </div>
        <ul
          className={classNames("header-menu-group", {
            active: isToggle,
          })}
        >
          <li>솔루션</li>
          <li>서비스</li>
          <li>개발스택</li>
          <li>산출물</li>
          <li>회사연혁</li>
          <li>고객사</li>
        </ul>
      </nav>
    </header>
  );
}
